@import "../../styles/vars";

.login {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;

  &__form {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;

    &_phone, &_code {
      width: 300px;
      height: 100px;
      margin: 0 !important;
    }

    &_bottom {
      margin-top: 8px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 12px;

      a {
        font-size: 16px;
        color: @layout;
      }
    }
  }
}

@height-base: 40px;@height-lg: 45px;@border-radius-base: 5px;@text-color-secondary: rgba(0,0,0,.3);@radio-size: 20px;@input-bg: #f5f8fa;@select-background: #f5f8fa;@select-border-color: #f5f8fa;@input-border-color: #f5f8fa;@label-color: #a1a5b7;