@import '~antd/dist/antd.less';
@import 'vars.less';
@import "~reset-default-css/resetDefault.css";


body {
  font-size: 13px;
  font-family: 'Poppins', sans-serif;
  background: @main;
  overflow: hidden;
  ::-webkit-scrollbar {
    width: 4px;
  }
  ::-webkit-scrollbar-track {
    opacity: 0;
  }
  ::-webkit-scrollbar-thumb {
    background: @light-grey;
    border-radius: 20px;
  }
}
p{
  margin: 0;
  padding: 0;
}

.ant-menu-vertical {
  border: none;
}

.ant-popover-inner-content{
  padding: 0!important;
  border-radius: 8px!important;
}

.welcome-page {
  text-align: center;
  position: absolute;
  width: calc(80% - 265px);
  max-height: 90%;
  top: calc(50% + (65px / 2));
  left: calc(10% + 265px);
  transform: translateY(-50%);
  box-sizing: border-box;
  padding: 50px;
  border-radius: 10px;
  background: @white;
  z-index: 0;


  &-sub-text {
    margin-bottom: 0 !important;
    line-height: 1.8;
    font-size: 14px;
    font-weight: 500;
  }

  &-button {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    margin-top: 30px;

    &>img {
      width: 100%;
      margin-top: 30px;
      max-width: 300px;
    }
  }
}

.tabs {
  border-bottom: none;
}

.create-modal {
  top: 20px;
  .ant-modal-footer {
    text-align: center;
  }
}

.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgba(0,0,0,0.3);
  z-index: 9;
  transition: all .3s ease-in-out;
}

.menu-btn {
  display: none;
}

.search-input {
  width: 300px;
  outline: none;
  background-color: @white;
}

.cards-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 20px;

  .card {
    width: calc(33.33% - 20px);
    min-height: 180px;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    margin: 0 10px 20px;
    padding: 20px;
    box-sizing: border-box;
    background: @white;
    cursor: pointer;
    transition: .1s all;

    .header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 10px
    }

    .card-data {
      .title {
        font-size: 18px;
        font-weight: 500;
        color: @dark;
        margin: 0;
      }

      .description {
        color: @light-grey;
        margin-top: 0;
      }
    }

    .card-dates {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      padding: 0;
      margin: 20px 0;

      span {
        color: @dark;
      }

      li {
        width: calc(50% - 10px);
        border: 1px dashed #e4e6ef!important;
        min-width: 150px;
        padding: 10px;
        box-sizing: border-box;
        list-style: none;
        border-radius: 10px;
        font-size: .7rem;

        &>p {
          margin: 0;
          color: #b5b5c3!important;
          font-weight: 500;
        }
      }
    }

    .card-landings {
      position: relative;
      display: flex;

      .card-landing {
        margin-left: 2px;
      }

      .card-landing:first-child {
        margin-left: 0;
      }
    }

    &.add {
      justify-content: center;
      align-items: center;
      cursor: pointer;

      svg {
        font-size: 40px;
      }
    }

    .card-actions {
      display: flex;
      flex-direction: row-reverse;

      svg {
        font-size: 16px;
        margin-left: 10px;
        color: @grey;
        cursor: pointer;

        &:hover {
          color: @bs-blue;
        }
      }
    }

    &:hover {
      border: 2px solid @light-grey;
    }
  }
}

@height-base: 40px;@height-lg: 45px;@border-radius-base: 5px;@text-color-secondary: rgba(0,0,0,.3);@radio-size: 20px;@input-bg: #f5f8fa;@select-background: #f5f8fa;@select-border-color: #f5f8fa;@input-border-color: #f5f8fa;@label-color: #a1a5b7;