@import "../../styles/vars";

.header {
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  border-bottom: 2px solid @border;

  &__logo {
    padding: 12px;
    width: 320px;
    height: 100%;
    border-right: 2px solid @border;
  }

  &__chat-info {
    padding: 0 12px;
    width: calc(100vw - 320px);
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &_left {
      display: flex;
      align-items: center;
      gap: 8px;

      .caption {
        &_title {
          font-size: 16px;
          color: @layout;
        }

        &_subtitle {
          font-size: 12px;
          color: @layout;
        }
      }
    }

    &_right {
      display: flex;
      align-items: center;
      padding-right: 24px;

      .menu-icon {
        height: 35px;
        width: 35px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: @dark-light;
        border-radius: 50%;
        cursor: pointer;
        transition: all .5s;

        &:hover {
          transform: scale(1.05);
        }

        &:active {
          transform: scale(0.9);
        }

        svg {
          font-size: 30px;
          fill: @light-grey;

        }
      }
    }


  }
}

.header-menu {
  padding: 14px;
  width: 200px;
  height: auto;
  display: flex;
  flex-direction: column;
  background-color: @main;
  border-radius: 4px;
  color: white;
  border: 4px solid @bs-gray;

  &__item {
    width: 100%;
    height: 40px;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 12px;
    cursor: pointer;

    &_title {
      font-size: 16px;
    }

    &_icon {
      font-size: 16px;
    }
  }
}
