@import "../../styles/vars";

.bottom-menu {
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 70px;
  border-top: 2px solid @border;
  &__item {
    position: relative;
    display: flex;
    justify-content: center;
    svg {
      color: @bs-gray;
      font-size: 20px;
      margin-bottom: 3px;
    }
  }
}

@height-base: 40px;@height-lg: 45px;@border-radius-base: 5px;@text-color-secondary: rgba(0,0,0,.3);@radio-size: 20px;@input-bg: #f5f8fa;@select-background: #f5f8fa;@select-border-color: #f5f8fa;@input-border-color: #f5f8fa;@label-color: #a1a5b7;