@import "../../../styles/vars";

.chat {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;

  &__messages {
    margin:  0 0 8px 0;
    padding: 0 30px 30px 30px;
    overflow-y: auto;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 6px;

    &_message {

      .self {
        display: flex;
        justify-content: end;
        align-items: end;
        gap: 4px;

        .avatar {
          order: 2;
        }

        .caption {
          .captionTemplate;
          order: 1;
          background-color: saturate(@dark-light, 20%);

          &_tail {
            border-radius: 8px 8px 0 8px;
          }

          &_tail::after {
            .tailTemplate;
            right: -9px;
            background: inherit;
          }
        }
      }

      .received {
        display: flex;
        justify-content: start;
        align-items: end;
        gap: 4px;

        .avatar {
          order: 1;
        }

        .caption {
          .captionTemplate;
          order: 2;
          background-color: @dark-light;

          &_no-tail {
            margin-left: 40px;
          }

          &_tail {
            border-radius: 8px 8px 8px 0;
          }

          &_tail::after {
            .tailTemplate;
            left: -9px;
            transform: scale(-1, 1);
            background: inherit;
          }
        }
      }
    }
  }
}

.msg-input {
  margin-bottom: 8px;
  width: 100%;
  height: 56px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 12px;

  &__textArea {
    position: relative;
    width: 75%;
    height: 100%;

    //&::after {
    //  .tailTemplate;
    //  right: -9px;
    //  background-color: saturate(@dark-light, 20%);
    //}

    textarea {
      padding: 10px;
      width: 100%;
      height: 100%;
      font-size: 16px;
      line-height: 18px;
      //border-radius: 8px 8px 0 8px;
      border-radius: 8px;
      background-color: saturate(@dark-light, 20%);
      color: @layout;
      resize: none;
    }

    &_smile {
      position: absolute;
      top: 8px;
      right: 8px;
      fill: @light-grey;

      svg {
        border: 2px solid @light-grey
      }
    }
  }

  &__btn {
    position: relative;
    width: 56px;
    height: 56px;
    border-radius: 50%;
    background-color: saturate(@dark-light, 20%);
    transition: all .3s;

    &_active {
      cursor: pointer;

      &:hover {
        background-color: saturate(@dark-light, 10%);
      }

      &:active {
        transform: scale(0.9);
      }
    }

    svg {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-40%, -50%);
      width: 50%;
      height: 50%;
      color: @light-grey;
    }
  }

}

.tailTemplate {
  content: '';
  position: absolute;
  bottom: 0;
  display: block;
  height: 10px;
  width: 10px;
  z-index: -1;
  clip-path: url(#svgPath);
}

.captionTemplate {
  position: relative;
  padding: 2px 40px 30px 8px;
  max-width: 80%;
  height: auto;
  display: flex;
  flex-direction: column;
  border-radius: 8px;
  color: @layout;
  word-wrap: break-word;

  &_name {
    font-size: 20px;
    color: @light-grey;
    text-shadow: 0 0 6px @dark;
  }

  &_text {
    white-space: pre-line;
    color: @layout;
  }
  &_date{
    position: absolute;
    bottom: 4px;
    right: 4px;
  }
}
