@media (max-width: 1024px) and (min-width: 768px) {
  .cards-list .card {
     width: calc(50% - 20px);
  }
}

@media (max-width: 768px) and (min-width: 414px) {
  .cards-list .card {
     width: 100%;
  }

  .categories-list > label {
    width: 100%;
  }
}

@media (max-width: 768px) {
  .content {
    width: 100%;
    box-shadow: none;
  }

  .logo {
    img {
      height: 70px;
    }
  }

  .layout {
    padding-left: 0;
  }

  .sider {
    left: -265px;

    &.active {
      left: 0;
    }
  }

  .welcome-page {
    width: 100%;
    left: 0;
  }

  .menu-btn {
    display: block;
  }
}

