.profile {
  .flexTemplate;
  justify-content: center;
  flex: 1;
  width: 100%;
  height: 100%;

  &__container {
    width: 60%;
    height: 60%;
    .flexTemplate;
    justify-content: space-between;

    &_form {
      .flexTemplate;
      justify-content: center;

      .avatar {
        position: relative;
        cursor: pointer;
        margin-bottom: 12px;

        svg {
          fill: #fff;
          opacity: 0;
          position: absolute;
          bottom: 0;
          right: 0;
          transition: all .5s;
        }

        &:hover {
          svg {
            opacity: 1;
          }
        }
      }

      .form {
        width: 450px;
        height: 250px;
        display: flex;
        flex-direction: column;
        justify-content: start;
        gap: 22px;

        &__items {

          &_birthday {
  display: flex !important;
            justify-content: center !important;
          }
        }
      }
    }

    &_logout {

      .confirm {
        display: flex;
        gap: 12px;
      }
    }
  }
}

.flexTemplate {
  display: flex;
  flex-direction: column;
  align-items: center;
}
