@import "../../styles/vars";

.chats-list {
  padding: 8px;
  display: flex;
  flex-direction: column;
  gap: 6px;

  &__item {
    padding: 8px;
    display: flex;
    align-items: center;
    gap: 6px;
    transition: all .4s;
    cursor: pointer;
    border-radius: 8px;

    &_active {
      background-color: fadeout(@grey, 70%) !important;
    }

    &:hover {
      background-color: fadeout(@grey, 90%);
    }

    &_avatar {

    }

    &_caption {
      display: flex;
      flex-direction: column;
      justify-content: start;
      align-items: start;

      .user-name {
        font-size: 18px;
        color: @layout;
      }

      .last-message {
        width: 200px;
        font-size: 14px;
        color: @light-grey;
        white-space: nowrap; /* Текст не переносится */
        overflow: hidden; /* Обрезаем всё за пределами блока */
        text-overflow: ellipsis;
      }
    }
  }
}

