@import "../../styles/vars";

.users-list {
  padding: 12px 0;
  overflow-y: auto;
  height: calc(100vh - 120px);
  display: flex;
  justify-content: start;
  align-items: center;
  flex-direction: column;
  gap: 10px;

  &__item {
    padding: 8px;
    width: 85%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: @white;
    border-bottom: 1px solid @dark-semi-light;
    border-radius: 8px;
    transition: all .5s;

    &:hover {
      background-color: @dark-semi-light
    }

    &_caption {

      .nickName {
        font-size: 16px;
        margin-left: 10px;
      }

      .name {
        font-size: 12px;
        margin-left: 10px;
      }
    }

    &_message {
      margin-left: auto;
      transition: all .5s;

      svg{
        transition: all .5s;
        &:hover {
          transform: scale(1.05);
        }
        &:active {
          transform: scale(0.9);
        }
      }
    }
  }
}

@height-base: 40px;@height-lg: 45px;@border-radius-base: 5px;@text-color-secondary: rgba(0,0,0,.3);@radio-size: 20px;@input-bg: #f5f8fa;@select-background: #f5f8fa;@select-border-color: #f5f8fa;@input-border-color: #f5f8fa;@label-color: #a1a5b7;