@import "../../styles/vars";

.main {
  width: 100%;
  height: calc(100vh - 120px);
  display: flex;
  justify-content: space-between;

  &_panel-space {
    width: 320px;
    border-right: 2px solid @border;
  }

  &_chat-space {
    width: calc(100vw - 320px);
  }
}



